import hash from 'object-hash'
import React from 'react'

import TableOfContents from '../../components/TableOfContents'
import { contentRenderer } from '../../utils/renderUtils'

const Section = ({ images, sections }) => {
  const renderer = contentRenderer(images)

  const titles = []

  sections.forEach(({ type, item, meta }) => {
    if (type === 'section') {
      titles.push({ meta, item: item.find(({ type }) => type === 'title') })
    }
  })

  const rendered = sections.map(renderer)

  rendered.splice(2, 0, <TableOfContents key={hash(titles)} list={titles} />)

  return rendered
}

export default Section
