import { x } from '@xstyled/styled-components'
import hash from 'object-hash'
import React from 'react'

const Notes = ({ children }) => (
  <x.dl
    fontSize='0.875rem'
    mb='1.5rem'
    lineHeight='1.5'
    px={{ _: '16px', md: '0' }}
  >
    {children.map((note, i) => (
      <x.div key={hash(note)} display='flex'>
        <x.dt display='inline'>*{i + 1} </x.dt>
        <x.dd
          display='inline'
          ml='1em'
          dangerouslySetInnerHTML={{ __html: note }}
        />
      </x.div>
    ))}
  </x.dl>
)

export default Notes
