import hash from 'object-hash'
import React from 'react'

import Box from '../components/Box'
import CtaButton from '../components/CtaButton'
import Heading from '../components/Heading'
import HeadingCircle from '../components/HeadingCircle'
import HeadingImg from '../components/HeadingImg'
import HeadingLine from '../components/HeadingLine'
import Hero from '../components/Hero'
import Image from '../components/Image'
import Notes from '../components/Notes'
import Paragraph from '../components/Paragraph'
import Section from '../components/Section'
import SiteTitle from '../components/SiteTitle'

// eslint-disable-next-line react/display-name
export const contentRenderer = images => (layoutDescription, i) => {
  const { type, item, meta } = layoutDescription

  let image, mobileImage, desktopHero, mobileHero

  switch (type) {
    case 'hero':
      desktopHero = images.find(
        ({ node }) => node.relativePath === item.desktop
      )
      mobileHero = images.find(({ node }) => node.relativePath === item.mobile)

      return <Hero desktop={desktopHero?.node} mobile={mobileHero?.node} />
    case 'site_title':
      return <SiteTitle key={hash(item)}>{item}</SiteTitle>
    case 'heading':
      return <Heading key={hash(item)}>{item}</Heading>
    case 'heading_line':
      return <HeadingLine key={hash(item)}>{item}</HeadingLine>
    case 'heading_circle':
      return (
        <HeadingCircle key={hash(item)} order={meta.order}>
          {item}
        </HeadingCircle>
      )
    case 'paragraph':
      return <Paragraph key={hash(item)}>{item}</Paragraph>
    case 'img':
      image = images.find(({ node }) => node.relativePath === item.url)

      if (item.mobileUrl) {
        mobileImage = images.find(
          ({ node }) => node.relativePath === item.mobileUrl
        )
      }

      return (
        image && (
          <Image
            gatsbyImageNode={image?.node}
            gatsbyMobileImageNode={mobileImage?.node}
            caption={item.caption}
            ratio={meta?.ratio}
            link={meta?.link}
            key={hash(image.node)}
          />
        )
      )
    case 'box':
      return (
        <Box key={hash(item)}>
          {layoutDescription.item.map(contentRenderer(images))}
        </Box>
      )
    case 'section':
      return (
        <Section key={hash(item)} id={meta.id}>
          {layoutDescription.item.map(contentRenderer(images))}
        </Section>
      )
    case 'heading_img':
      return (
        <HeadingImg key={hash(item)}>
          {layoutDescription.item.map(contentRenderer(images))}
        </HeadingImg>
      )
    case 'ctaButton':
      return (
        <CtaButton link={item.link} key={hash(item)} color={meta.color}>
          {item.label}
        </CtaButton>
      )
    case 'notes':
      return <Notes key={hash(item)}>{item}</Notes>
    default:
      return null
  }
}
