import { x } from '@xstyled/styled-components'
import hash from 'object-hash'
import React from 'react'

import Heading from '../Heading'

const TableOfContents = ({ list }) => {
  const getSection = i => ('0' + (i + 1).toString()).slice(-2)

  return (
    <x.div bg='var(--theme-primary)' p={{ _: '16px' }} mb='1.5rem'>
      <Heading>このページで分かること</Heading>
      <nav>
        {list.map(({ item, meta }, i) => (
          <x.div key={hash(item.item)} mb='0.75rem'>
            <x.span mr='0.75rem'>{getSection(i)}</x.span>
            <x.a color='inherit' href={`#${meta.id}`}>
              {item.item}
            </x.a>
          </x.div>
        ))}
      </nav>
    </x.div>
  )
}

export default TableOfContents
