import styled, { x } from '@xstyled/styled-components'
import { GatsbyImage, getImage, withArtDirection } from 'gatsby-plugin-image'
import React from 'react'

import AspectBox from '../AspectBox'

const StyledImg = styled(GatsbyImage)`
  @media (max-width: sm) {
    width: 100%;
    height: 0;
    padding-bottom: ${(100 / 750) * 486}%;
  }
`

const Image = ({
  gatsbyImageNode,
  gatsbyMobileImageNode,
  caption,
  ratio,
  link
}) => {
  const gatsbyImage = getImage(gatsbyImageNode)

  let gatsbyMobileImage, image

  if (gatsbyMobileImageNode) {
    gatsbyMobileImage = getImage(gatsbyMobileImageNode)

    if (gatsbyImage && gatsbyMobileImage) {
      image = withArtDirection(gatsbyImage, [
        { media: '(max-width:640px)', image: gatsbyMobileImage }
      ])
    } else {
      image = gatsbyImage
    }
  } else {
    image = gatsbyImage
  }

  image = <StyledImg image={image} alt={caption ?? ''} />

  image = ratio ? <AspectBox ratio={ratio}>{image}</AspectBox> : image

  image = link ? (
    <a href={link} target='_blank' rel='noreferrer'>
      {image}
    </a>
  ) : (
    image
  )

  return caption ? (
    <x.figure mb='1.5rem'>
      {image}
      <x.figcaption
        fontSize='0.875rem'
        lineHeight='1.5'
        px={{ _: '16px', md: '0' }}
        dangerouslySetInnerHTML={{ __html: caption }}
      />
    </x.figure>
  ) : (
    <x.div mb='1.5rem'>{image}</x.div>
  )
}

export default Image
