import styled, { x } from '@xstyled/styled-components'
import React from 'react'

const Container = styled.divBox`
  width: 100%;
  height: 0;
  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  .gatsby-image-wrapper {
    width: 100%;
    height: 100%;
  }
`

const AspectBox = ({ ratio, children }) => (
  <Container pb={`${100 / ratio}%`}>
    <x.div position='absolute' top='0' right='0' bottom='0' left='0'>
      {children}
    </x.div>
  </Container>
)

export default AspectBox
