import styled from '@xstyled/styled-components'
import React from 'react'

const Button = styled.aBox`
  margin-bottom: 1rem;
  :last-of-type {
    margin-bottom: 4rem;
  }
`

const CtaButton = ({ children, link, color }) => (
  <Button
    href={link}
    target='_blank'
    rel='noreferrer'
    display='block'
    p='16px'
    w={{ _: 'calc(100% - 32px)', md: '320px' }}
    mx={{ _: '16px', md: 'auto' }}
    bg={color === 'orange' ? 'var(--gp-orange)' : 'var(--gp-blue)'}
    color='#fff'
    fontWeight='bold'
    textDecoration='none'
    borderRadius='md'
    textAlign='center'
  >
    {children}
  </Button>
)

export default CtaButton
